import React from 'react'
import styled from '@emotion/styled'
import dimensions from '../../styles/dimensions'
import { Link } from 'gatsby'

const NavigationContainer = styled.nav`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  position: relative;
  top: 0;
  left: 0;
  width: auto;
  border-bottom: none;
  @media(max-width: ${dimensions.tablet}px) {
    flex-direction: column;
    position: fixed;
    top: 5rem;
    right: 0;
    width: 100%;
    z-index: 40;
    background-color: white;
    padding-bottom: 2rem;
    box-shadow: 0px 8px 20px -4px rgba(0, 0, 0, 0.1);
  }
`

const NavigationLink = styled(Link)`
  color: var(--text-accent);
  text-decoration: none;
  position: relative;
  text-align: center;
  margin-bottom: 0;
  transition: 150ms;
  display: block;
  font-size: 1.6rem;
  font-weight: normal;
  margin: 0 15px; 

  
  @media(max-width: ${dimensions.tablet}px) {
    margin-left: 0;
    margin-bottom: var(--space);
    font-size: 1.6rem;
  }

  &::before {
    display: block;
    content: attr(title);
    font-weight: bold;
    height: 0;
    overflow: hidden;
    visibility: hidden;
  }

  &:after {
    content: '';
    display: inline-block;
    width: 0;
    height: 2px;
    background: var(--color-accent);
    position: absolute;
    left: 0;
    bottom: -10px;
    opacity: 0;
    transition: .3s ease-in-out;
  }
  &:hover,
  &.active {
    color: var(--color-accent);
    &:after {
      opacity: 1;
      bottom: -10px;
      width: 100%;

      @media(max-width: ${dimensions.mobile}px) {
        width: 100%;
      }
    }
  }
  &.active {
    font-weight: bold;
  }
`

const NavigationButtonBook = styled(Link)`
background:var(--color-accent);
border-radius: 23px;
color: #fff;
display: inline-block;
padding: 11px var(--space);
text-decoration: none;
font-weight: bold;
text-align: center;
transition: all 200ms;
font-size: 1.125rem;
line-height:1.375rem;
text-transform:uppercase;
letter-spacing: 0.54px;
font-weight: 900;
border: solid 2px transparent;
margin: 0 15px; 
`

const NavigationButton = styled(Link)`
  background:transparent;
  border-radius: 23px;
  color: var(--color-accent);
  display: inline-block;
  padding: 11px var(--space);
  text-decoration: none;
  font-weight: bold;
  text-align: center;
  // box-shadow: 0px 8px 20px -4px var(--color-accent);
  transition: all 200ms;
  font-size: 1.125rem;
  border: solid 2px var(--color-accent);
  line-height:1.375rem;
  text-transform:uppercase;
  letter-spacing: 0.54px;
  font-weight: 900;
  
`
// const callSales = styled(div)
const menuItems = [
  {
    id: 'home',
    name: 'Home',
    path: '/'
  },
  {
    id: 'product',
    name: 'Product',
    path: '/product/'
  },
  {
    id: 'why-auctm',
    name: 'Why Auctm',
    path: '/why-auctm/'
  },
  {
    id: 'pricing',
    name: 'Pricing',
    path: '/pricing/'
  }
]

export default function Navigation () {
  return (
    <>
      <NavigationContainer>
        {menuItems.map((menu, index) => (
          <NavigationLink
            id={menu.id}
            to={menu.path}
            aria-label={menu.name}
            activeClassName='active'
            key={`${menu.link}${index}`}
          >
            {menu.name}
          </NavigationLink>
        ))}
        <div className="bookCallDiv">
        <NavigationButtonBook id='book-demo' to='/' target='_blank' rel='noreferrer noopener' aria-label='BookDemo'>Book A Demo</NavigationButtonBook>
        <div className="callSales">or call sales <a href="telto:(000) 000-0000">(000) 000-0000</a></div>
        </div>
        <NavigationButton id='app-login' to='/' target='_blank' rel='noreferrer noopener' aria-label='Login'>
                    Login
        </NavigationButton>
      </NavigationContainer>
    </>
  )
}
